.task-card {
  background-color: white;
  border-radius: 3px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 12px;
  margin-bottom: 8px;
  user-select: none;
  transition: background-color 0.2s ease;
}

.task-card.dragging {
  background-color: #f4f5f7;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.task-title {
  font-size: 14px;
  font-weight: 500;
  color: #172b4d;
  margin: 0;
  word-break: break-word;
}

.priority-badge {
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 3px;
  font-weight: 500;
  text-transform: capitalize;
}

.priority-badge.high {
  background-color: #ffebe6;
  color: #de350b;
}

.priority-badge.medium {
  background-color: #fffae6;
  color: #ff991f;
}

.priority-badge.low {
  background-color: #e3fcef;
  color: #006644;
}

.task-description {
  font-size: 13px;
  color: #5e6c84;
  margin: 0 0 12px 0;
  word-break: break-word;
}

.task-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.task-date {
  font-size: 12px;
  color: #5e6c84;
}

.task-card:hover {
  background-color: #f4f5f7;
}